"use client"
import { useForm } from "react-hook-form";
import {
    CardNumberElement,
    CardExpiryElement,
    CardCvcElement,
    useStripe,
    useElements,
    PaymentElement,
} from "@stripe/react-stripe-js";
import { User } from "lucide-react";
import { FormEvent, useEffect, useState } from "react";
import axios from "axios";
import { convertToSubcurrency } from "@/utils/payment";

export default function PaymentCard({ amount }: { amount: number }) {
    const stripe = useStripe();
    const elements = useElements();
    const [clientSecret, setClientSecret] = useState<string>("");
    const [loading, setLoading] = useState<boolean>(false);
    const [error, setError] = useState<string | null>(null);
    useEffect(() => {
        const getClientSecret = async () => {
            try {

                const res = await axios.post("http://localhost:5200/api/payment/payment-intent", { amount: convertToSubcurrency(amount) });
                setClientSecret(res.data.clientSecret);
            } catch (err) {
                console.error(err);
            }
        }
        getClientSecret();
    }, [amount])

    const handleSubmit = async (e: FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        setLoading(true);
        if (!stripe || !elements) return;

        const { error: submitError } = await elements.submit();
        if (submitError && submitError.message) {
            setError(submitError.message);
            setLoading(false);
        }

        const { error } = await stripe.confirmPayment({
            elements,
            clientSecret,
            confirmParams: {
                return_url: `http://localhost:3000/order/sucess/${amount}`
            }
        })
        if (error.message && error) {
            setError(error.message);
        } else {

        }
    }



    return (
        <>
            {(!clientSecret || !stripe || !elements) ? (<div className="animate-spin rounded-full h-12 w-12 border-t-4 border-blue-500 border-solid mx-auto"></div>) : (
                <div className="shadow-xs border-borderColor border-[1px] flex flex-col gap-4 rounded-xl py-7 px-6 ">
                    <div className=" flex flex-col gap-3">
                        <h2 className="text-2xl leading-8 font-bold text-secondary">Your Details</h2>
                        <p className="text-gray text-base! leading-7 "></p>
                    </div>
                    <form className="flex flex-col gap-6" onSubmit={handleSubmit}>

                        {clientSecret && <PaymentElement className="" />}
                        <button disabled={loading || error != null} type="submit" className="btn py-3 disabled:bg-gray! disabled:cursor-auto!  px-2 max-w-[150px] w-full">{loading ? (<span>Processing...</span>) : error ? (<span>Invalid data</span>) : (<span>Pay ${amount}</span>)}</button>
                    </form>
                </div>

            )}

        </>
    );
}
